body {
  color: var(--tg-theme-text-color);
  background: var(--tg-theme-bg-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 18px;
}

.hint {
  color: var(--tg-theme-hint-color);
}

.link {
  color: var(--tg-theme-link-color);
}

.button {
  background: var(--tg-theme-button-color);
  color: var(--tg-theme-button-text-color);
  border: none;
  font-size: 18px;
}

.button:not(:last-child) {
  margin-bottom: 20px;
}

#usercard {
  text-align: center;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.block {
  margin: 50px 0;
  padding: 10px;
  max-width: 90vw;
  width: 100vw;
  overflow-x: auto;
  border: 1px solid black;
  min-height: 80vh;
  background-color: rgb(255, 255, 255);
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
  background-color: rgb(215, 215, 215);
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 10px;
}

.card {
  padding: 15px;
  width: 288px;
}

.card-text {
  margin-top: 0;
  margin-bottom: 10px;
}

.card-img-top {
  display: flex;
  justify-content: center;
}

.search-form {
  padding: 10px;
}

.card {
  margin: 10px;
}

.fb {
  width: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.fb-v {
  flex-direction: column;
}

header {
  padding: 15px;
}

.hero-body {
  margin: 20px 0 0 0;
}

.hero-bio {
  height: 150px;
  overflow: auto;
}
